import React, { useEffect, useRef } from 'react';
import './search-component.css'
import {ReactComponent as SearchSvg} from './search.svg'

export function SearchComponent(props) {

    let selector = useRef(null)

    function search(searchedText){
        document.querySelectorAll('div.streamer-component').forEach ( e => {
            const findIt = (element) => {
                let cleanSearchText = searchedText.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase().replace(/[^a-zA-Z0-9]/g,'');
                if(searchedText.length >= 3 && element.innerText.toLowerCase().replace(/[^a-zA-Z0-9]/g,'').includes(cleanSearchText)){
                    return true
                } else {
                    return false
                }
            }

            if(Array.from(e.querySelectorAll('div.logoSide span')).some(findIt)) {
                e.classList.add('found')
            } else {
                e.classList.remove('found')
            }

        })
        
    }

    useEffect(() => {
        selector.current.addEventListener('input',(e)=>{
            search(e.target.value)
        })
        selector.current.addEventListener('click',(e)=>{
            let inputSearch = selector.current.querySelectorAll('input')
            if(inputSearch){
                console.log(inputSearch)
                inputSearch[0].focus()
            }
        })
    })

    return (
        <div 
            className = 'searchDiv'
            ref =  {selector}
        >
            <SearchSvg/> <input className = 'searchBox' type='text' spellCheck="false"/>
        </div>
    );
}
  