import React, {useEffect, useRef} from 'react';
import './link-component.css'

export function LinkComponent(props) {

    let selector = useRef(null)

    function openLink(url_) {
        var win = window.open(url_, '_blank');
        win.focus();
    }

    useEffect(() => {
        if(selector.current) {
            selector.current.style.setProperty('--posx',props.position.x+'vw');
            selector.current.style.setProperty('--posy',props.position.y*0.5+'vw');
            selector.current.style.setProperty('--opacity', 1);
        }
        
    
      });

    return (
        <img 
            ref =  {selector}
            className = {'linkComp '+props.id}
            src = {props.image}
            onClick = {() => openLink(props.link)}
        />
    );
}
  